import React from 'react';
import {
  StyledSection,
  SectionTitle,
  SectionContent,
  SectionImage,
  SectionText,
} from './section.styles';

interface SectionProps {
  title: string;
  image: 'about' | 'values' | 'sharing';
  content: string;
}

export const Section = ({ title, image, content }: SectionProps) => {
  return (
    <StyledSection className="about-section">
      <SectionTitle>{title}</SectionTitle>
      <SectionContent>
        <SectionImage src={`${process.env.ASSET_PREFIX}/about/${image}.svg`} />
        <SectionText>{content}</SectionText>
      </SectionContent>
    </StyledSection>
  );
};
